<template>
  <div
    class="xmcard"
    :class="{
      [`chart-item-${item.id}`]: true
    }"
  >
    <xmbase-block-item
      :item="{ label }"
      @ask-panel="handlePanel"
    >
      <slot />
    </xmbase-block-item>
  </div>
</template>

<script>
import sub from './sub-unsub'

export default {
  name: 'xmcard-panel',
  props: {
    label: {
      type: String,
      default: '卡片标题'
    },
    item: {
      type: Object,
      default () {
        return {}
      }
    },
    thirdAppId: String,
    relativeUrl: String
  },
  components: {
  },
  methods: {
    handlePanel () {
      sub(this.item, this.thirdAppId, this.relativeUrl)
    }
  }
}
</script>

