import Index from './index.vue'
import { menu } from './data'
import Icon from './icon.vue'

menu.map(it => {
  return window.Vue.component(it.name, {
    name: it.name,
    render (h) {
      const click = (name) => {
        this.$emit('click', name)
      }
      return h(Icon, {
        props: {
          icon: it.icon,
          label: it.label,
          name: it.name
        },
        on: {
          click
        }
      })
    }
  })
})

const Constructor = window.Vue.extend(Index)
let instance
export default (item, thirdAppId, relativeUrl) => {
  if (!instance) {
    instance = new Constructor({
      el: document.createElement('div')
    })
    document.body.appendChild(instance.$el)
  }
  instance.show = true
  instance.item = item
  instance.thirdAppId = thirdAppId
  instance.relativeUrl = relativeUrl
}
