import domtoimage from 'dom-to-image'

export const getLocal = async () => {
  let local = await window.xm.getStorage({ key: 'local-charts-data', isGlobal: true })
  if (local) {
    try {
      local = JSON.parse(local)
    } catch (e) {
      console.log(local)
      local = []
    }
  } else {
    local = []
  }
  return local
}
const setLocal = async (arr) => {
  await window.xm.setStorage({ key: 'local-charts-data', value: JSON.stringify(arr), isGlobal: true })
}
