function getImage (cardId, scale = 1) {
  return new Promise((resolve) => {
    const domNode = document.querySelector(`.chart-item-${cardId}`)
    if (!domNode) {
      return console.warn('xmcard domNode 没有获取到')
    }
    domtoimage
      .toBlob(domNode, {
        bgcolor: '#fff',
        width: domNode.clientWidth * scale,
        height: domNode.clientHeight * scale,
        style: {
          transform: 'scale(' + scale + ')',
          transformOrigin: 'top left'
        },
        filter: node => {
          if (!node.classList) return true
          const cl = node.classList
          // 过滤三个点
          // 过滤专项的三个操作
          return !cl.contains('charts-block-item__trigger') &&
            !cl.contains('charts-block-item__opt') &&
            !cl.contains('sub-unsub-detail')
        }
      })
      .then((blob) => {
        const fileReader = new FileReader()
        fileReader.onload = e => {
          resolve(e.target.result.split(',')[1])
        }
        fileReader.readAsDataURL(blob)
      })
  })
}

const getRoutePath = () => location.hash.split('?')[0]

export const menuActions = {
  async subscribe (item) {
    const { type, label, bgReal, bg, baseComponentNames, dataArr } = item
    const arr = await getLocal()
    const routePath = getRoutePath()
    const { name, appId, color, appIcon } = await window.xm.native('getappinfo')
    arr.push({
      appId,
      color,
      type,
      routePath,
      label,
      bgReal,
      bg,
      baseComponentNames,
      dataArr
    })
    await setLocal(arr)
    window.xm.showToast({
      message: '订阅成功！'
    })
  },
  async unsubscribe (cardId) {
    const arr = await getLocal()
    const i = arr.findIndex(x => x.id === cardId)
    arr.splice(i, 1)
    await setLocal(arr)
    window.xm.showToast({
      message: '取消订阅成功！'
    })
  },
  async createRedlight (item, redLightAppId, relativeUrl) {
    const { type, id } = item
    const base64 = await getImage(id, 2)
    const data = await window.xm.native('uploadimg', { base64 })
    const { name, appId, color, appIcon } = await window.xm.native('getappinfo')
    const routePath = getRoutePath()
    window.xm.openApp({
      appId: redLightAppId || '70640883',
      relativeUrl: relativeUrl || 'index.html#/page/red-light/page-1588732321267',
      param: {
        img: encodeURIComponent(data),
        appId,
        color,
        type,
        path: encodeURIComponent(routePath)
      }
    })
  },
  async createUrge (item, urgeAppId, relativeUrl) {
    const { type } = item
    const routePath = getRoutePath()
    const { name, appId, color, appIcon } = await window.xm.native('getappinfo')
    window.xm.openApp({
      appId: urgeAppId || '72385353',
      relativeUrl: relativeUrl || 'index.html#/new-form/5/add/0',
      param: {
        name: encodeURIComponent(name),
        ioc: encodeURIComponent(JSON.stringify({
          appId,
          color,
          type,
          path: routePath
        }))
      }
    })
  },
  async createSign (item) {
    const { id } = item
    const base64 = await getImage(id)
    const img = await window.xm.native('annotate', { base64 })
    await window.xm.native('shareimg', { base64: img })
  },
  call (telNumber) {
    window.xm.makePhoneCall({
      dialNumber: telNumber
    })
  },
  share (item) {
    const { id } = item
    return new Promise((resolve) => {
      window.xm.showLoading()
      setTimeout(() => {
        window.xm.hideLoading()
        getImage(id).then(base64 => {
          window.xm.native('shareimg', { base64 })
          resolve()
        })
      }, 300)
    })
  },
  async createWarning (item, warningAppId, relativeUrl) {
    const { id, type } = item
    const routePath = getRoutePath()
    const base64 = await getImage(id, 2)
    const data = await window.xm.native('uploadimg', { base64 })
    const { name, appId, color, appIcon } = await window.xm.native('getappinfo')
    window.xm.openApp({
      appId: warningAppId || '38842751',
      relativeUrl: `${relativeUrl || 'index.html#/page/ioc-warning/page-1590635420653'}?img=${encodeURIComponent(data)}&appId=${appId}&color=${color}&type=${type}&path=${routePath}`
    })
  },
  toDataFrom (item, appId) {
    const { id } = item
    window.xm.openApp({
      appId: appId || '59046561'
    })
  },
  openMonitor (item, appId) {
    const { id } = item
    window.xm.openApp({
      appId: appId || '62344381'
    })
  }
}

