<template>
  <div class="empty-render">
    <panel v-bind="data">
      <component :is="name" v-bind="data" />
    </panel>
  </div>
</template>

<script>
import Panel from './panel.vue'

export default {
  name: 'xmcard-render',
  components: {
    Panel
  },
  props: {
    name: {
      type: String,
      required: true
    },
    data: {
      type: Object,
      default () {
        return {
        }
      }
    }
  },
  created () {
    window.xmcard = window.xmcard || {}
    window.xmcard.vmCenter = window.xmcard.vmCenter || {}
    // name 是组件名称、同时也是组件 id
    window.xmcard.vmCenter[this.name] = this
  },
  methods: {
    reload () {
      this.$forceUpdate()
    }
  }
}
</script>
