<template>
  <div
    v-show="show"
    class="sub-unsub"
    @click="show = false"
    @touchmove.prevent=""
  >
    <transition name="slide-up">
      <div
        v-show="show"
        class="sub-unsub__menu"
        @click.stop=""
      >
        <div class="sub-unsub__menu-title" style="height: 57px">
          卡片操作
        </div>
        <div class="sub-unsub__menu-cross" @click="show = false">
          <img width="16px" src="data:image/jpeg;base64," alt="">
        </div>
        <div class="grid">
          <xmcard-red @click="handleOpt" />
          <xmcard-urge @click="handleOpt" />
          <xmcard-call @click="handleOpt" />
          <xmcard-sign @click="handleOpt" />
          <xmcard-unsub v-if="hasSub" @click="handleOpt" />
          <xmcard-sub v-else @click="handleOpt" />
          <xmcard-warn @click="handleOpt" />
          <xmcard-share @click="handleOpt" />
          <xmcard-data @click="handleOpt" />
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { getLocal, menuActions } from './data'
export default {
  components: {
  },
  data () {
    return {
      show: false,
      hasSub: false,
      item: null,
      thirdAppId: undefined,
      relativeUrl: undefined
    }
  },
  watch: {
    show (val) {
      if (val) {
        // 检查是否订阅
        getLocal().then(arr => {
          this.hasSub = arr.some(x => x.id === this.item.id)
        })
      }
    }
  },
  methods: {
    handleOpt (name) {
      const handle = (fn) => {
        fn(this.item, this.thirdAppId, this.relativeUrl)
      }
      console.log(name)
      switch (name) {
        case 'xmcard-red': {
          return handle(menuActions.createRedlight)
        }
        case 'xmcard-urge': {
          return handle(menuActions.createUrge)
        }
        case 'xmcard-call': {
          return handle(menuActions.call)
        }
        case 'xmcard-sign': {
          return handle(menuActions.createSign)
        }
        case 'xmcard-unsub': {
          return handle(menuActions.unsubscribe)
        }
        case 'xmcard-sub': {
          return handle(menuActions.subscribe)
        }
        case 'xmcard-warn': {
          return handle(menuActions.createWarning)
        }
        case 'xmcard-share': {
          return handle(menuActions.share)
        }
        case 'xmcard-data': {
          return handle(menuActions.toDataFrom)
        }
      }
    }
  }
}
</script>

